html,
body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 1.5;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}

.manage-inventory__row {
    display: block;
    width: 380px;
    margin: 0 auto;
}
.manage-inventory {
    position: relative;
    z-index: 4;
    background-color: transparent;
    color: #D6E0E7;
    display: flex;
    flex-wrap: wrap;
    place-content: center;
    margin-top: 70px;
    flex-direction: column;
}

.manage-inventory__alert {
    z-index: 1;
    position: absolute;
    top: 65px;
    right: 10px;
}

.manage-inventory-list, .manage-inventory-actions {
    display: block;
}

.manage-inventory-actions {
    padding-top: 10px;
    border-top: 1px solid rgba(68.808, 73.376, 86.135, 0.4);
}


.manage-inventory select {
    color: rgba(0, 0, 0, 0.87);
    box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);
    background-color: #e0e0e0;
    padding: 6px 16px;
    font-size: 0.875rem;
    box-sizing: border-box;
    -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    line-height: 24px;
    height: 24px;
    border-radius: 4px;
    letter-spacing: 0.02857em;
    text-transform: uppercase;
    max-width: 195px;
    min-width: 195px;
}

.manage-inventory button {
    margin: 0 5px 0 0;
}

.manage-inventory-item-container {
    display: flex;
    margin-bottom: 10px;
    margin-top: 10px;
}

.manage-inventory__quantity {
    margin-left: 20px;
    margin-top: 6px;
}

.manage-inventory__quantity-input-label {
    color: white !important;
}

.manage-inventory__quantity-change {
    display: flex;
    justify-content: flex-start;
}

.manage-inventory__user-list {
    margin-bottom: 10px;
}

.manage-inventory__title {
    font-weight: 700;
    margin-bottom: 5px;
}

.server-purchase {
    position: relative;
    z-index: 4;
    background-color: transparent;
    color: #D6E0E7;
    display: flex;
    flex-wrap: wrap;
    place-content: center;
    margin-top: 70px;
    flex-direction: column;
}

.server-purchase__alert {
    z-index: 1;
    position: absolute;
    top: 65px;
    right: 10px;
}

.entitlement-item {
    position: relative;
    -webkit-transition: opacity 150ms ease-out, -webkit-transform 350ms ease-out;
    transition: opacity 150ms ease-out, -webkit-transform 350ms ease-out;
    transition: transform 350ms ease-out, opacity 150ms ease-out;
    transition: transform 350ms ease-out, opacity 150ms ease-out, -webkit-transform 350ms ease-out;
}

.entitlement-item.initialFlow1 {
    opacity: 0;
    -webkit-transform: translateY(20px) scale(1);
            transform: translateY(20px) scale(1);
}

.entitlement-item-card {
    color: #D6E0E7;
    display: flex;
    flex-direction: column;
    max-width: 300px;
    min-width: 300px;
    height: 300px;
    margin: 16px;
    flex-shrink: 0;
    flex-grow: 1;
    flex-base: 50%;
    background: transparent;
    -webkit-transition: box-shadow 0.3s ease-in-out;
    transition: box-shadow 0.3s ease-in-out;
    border-radius: 8px;
    padding: 8px;
}

.entitlement-item-card:hover {
    box-shadow: 0 5px 12px 0px rgba(0,0,0,0.2);
}

.entitlement-item-card-media {
    display: block;
    margin-top: 8px;
    min-height: 120px;
    flex-grow: 1;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

.entitlement-item-card-content {
    min-height: 66px;
    overflow: hidden;
    flex-grow: 0;
    padding: 16px;
    padding-bottom: 0;
    margin-bottom: 8px;
}

.entitlement-item-card-content-title {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-weight: 700;
}

.entitlement-item-card-content-description {
    display: block;
    display: -webkit-box;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    -webkit-line-clamp: 3;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 400;
}

.entitlement-list {
    position: relative;
    z-index: 4;
    background-color: transparent;
    color: #D6E0E7;
    display: flex;
    flex-wrap: wrap;
    place-content: center;
    margin-top: 70px;
}

.entitlement-redeem-form {
    position: relative;
    z-index: 4;
    background-color: transparent;
    color: #D6E0E7;
    display: flex;
    flex-wrap: wrap;
    place-content: center;
    margin-top: 70px;
    flex-direction: column;
    align-items: center;
}

.entitlement-redeem-input {
    color: black;
    margin-bottom: 10px;
    margin-top: 10px;
    text-align: center;
}

.entitlement-redeem-button {
    display: block;
}

.entitlement-redeem-error {
    color: red;
    margin-bottom: 20px;
}
.entitlement-redeem-loader {
    margin-top: 10px;
    margin-bottom: 10px;
}
